// Ant Design Resources
import { Space } from 'antd';

// Components
import { Step } from 'components/steps';
import { Instruction, Title } from 'components/text';
import { Translate } from 'components/language';
import { Card } from 'components/cards';

import { useMock } from 'hooks';
import { mockBets } from './utils/mock';
import { Brackets } from './components/Brackets';
import { BetsForm } from './components/BetsForm';
import { Challenge } from './components/Challenge';

type StepMakeYourBetsProps = {
  onSubmitBets: GenericFunction;
  challenge: DefaultTextCard;
  brackets: WBracket[];
};

export function StepMakeYourBets({ onSubmitBets, challenge, brackets }: StepMakeYourBetsProps) {
  useMock(() => {
    onSubmitBets(mockBets(brackets));
  });

  return (
    <Step fullWidth>
      <Title size="medium">
        <Translate pt="Faça suas apostas!" en="Make your bets!" />
      </Title>

      <Challenge challenge={challenge} />

      <Instruction contained>
        <Translate
          pt={
            <>
              Selecione um competidor para cada uma das 3 seções do torneio:
              <br />
              Quartas-de-finais: Ganhe 1 ponto se o competidor passar
              <br />
              Semi-finais: Ganhe 3 pontos se o competidor passar
              <br />
              Final: Ganhe 5 pontos se o competidor ganhar
            </>
          }
          en={
            <>
              Select a contender of each of the section in the Championship:
              <br />
              Quarter-finals: Get 1 point if the contender passes
              <br />
              Semi-finals: Get 3 point if the contender passes
              <br />
              Finals: Get 5 point if the contender wins
            </>
          }
        />
      </Instruction>

      <Brackets brackets={brackets} activeTier="quarter" />

      <BetsForm brackets={brackets} onSubmitBets={onSubmitBets} />
    </Step>
  );
}
