import { Button, Form, Select } from 'antd';
import { Translate } from 'components/language';
import { useLanguage } from 'hooks';
import { orderBy } from 'lodash';
import { useState } from 'react';

type BetsFormProps = {
  brackets: WBracket[];
  onSubmitBets: GenericFunction;
};

export function BetsForm({ brackets, onSubmitBets }: BetsFormProps) {
  const { language } = useLanguage();
  const [bets, setBets] = useState({ quarter: '', semi: '', final: '' });

  const updateBet = (value: PlainObject) => {
    setBets((s) => ({ ...s, ...value }));
  };

  const contenders = orderBy(
    brackets.filter((entry) => entry.tier === 'quarter').map((entry) => ({ id: entry.id, name: entry.name })),
    `name.${language}`
  );

  return (
    <Form
      className="w-bet-form"
      onValuesChange={updateBet}
      onFinish={onSubmitBets}
      labelCol={{ span: 12 }}
      wrapperCol={{ span: 12 }}
      initialValues={{ quarter: '', semi: '', final: '' }}
    >
      <Form.Item label={<Translate pt="Quartas de finais" en="Quarterfinals" />} name="quarter" required>
        <Select>
          <Select.Option value="">
            <Translate pt="Selecione" en="Select" />
          </Select.Option>
          {contenders.map((contender) => (
            <Select.Option value={contender.id} key={`quarter-${contender.id}`}>
              {contender.name[language]}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>

      <Form.Item label={<Translate pt="Semifinais" en="Semifinals" />} name="semi" required>
        <Select>
          <Select.Option value="">
            <Translate pt="Selecione" en="Select" />
          </Select.Option>
          {contenders.map((contender) => (
            <Select.Option value={contender.id} key={`semi-${contender.id}`}>
              {contender.name[language]}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>

      <Form.Item label={<Translate pt="Final" en="Final" />} name="final" required>
        <Select>
          <Select.Option value="">
            <Translate pt="Selecione" en="Select" />
          </Select.Option>
          {contenders.map((contender) => (
            <Select.Option value={contender.id} key={`final-${contender.id}`}>
              {contender.name[language]}
            </Select.Option>
          ))}
        </Select>
      </Form.Item>

      <Form.Item wrapperCol={{ offset: 12, span: 16 }}>
        <Button type="primary" htmlType="submit" disabled={!bets.final || !bets.quarter || !bets.semi}>
          <Translate pt="Enviar apostas" en="Submit bets" />
        </Button>
      </Form.Item>
    </Form>
  );
}
